.todoItem{
  background-color: #fafafa;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  width: 400px;
  border-radius: 20px;
}
.TodoItem-p{
  margin: 24px 0 24px 24px;
  width: calc(100%-120px);
  font-size: 1.8rem;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete{
  text-decoration: line-through;
}
.Icon{
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px;
  font-size: 16px;
  font-weight: bold;
  background-color: rgba(61, 61, 61, 0.774);
  border-radius: 50%;
  color: white;
}
.Icon-check{
  position: absolute;
  left: 12px;
}
.Icon-check--active{
  color: #4caf50;
}
.Icon-delete{
  position: absolute;
  top: -12px;
  right: 0;
}
.Icon-delete:hover{
  color: rebeccapurple;
}